import React, { Component } from 'react';
import Countdown from 'react-countdown';
import TimeForm from './TimeForm';

class Timer extends Component {
  constructor(props){
    super(props)
    this.state = {
      hours: 0,
      minutes: 5,
      seconds: 0
    }
    
    this.clockRef = React.createRef();
  }

  showForm = () => {
    let form = document.querySelector('#time-form');

    form.style.display = 'block';
  }

  convertToSeconds = () => {
    const {seconds, minutes, hours} = this.state;
    let total = parseInt(seconds);
    if (minutes !== 0) total += parseInt(minutes * 60);
    if (hours !== 0) total += parseInt(hours * 3600);
    return total;
  }

  formCallback = (hour, min, sec) => {
    this.setState({
      hours: hour,
      minutes: min,
      seconds: sec,
      alarm: false
    })
  }

  beep = () => {
    let beep = new Audio("data:audio/wav;base64,");
    beep.play();
  }

  startBeep = () => {
    let beepId = setInterval(() => this.beep(), 100);
    this.setState({
      beepId: beepId,
      alarm: true
    })
  }

  stopBeep = () => {
    if (this.state.beepId) clearInterval(this.state.beepId)
    this.setState({
      alarm: false
    })
  }

  resetCountdown = () => {

    this.setState({});

    this.clockRef.current.api.start();
  }

  silenceButtonClasses = () => {
    return this.state.alarm ? 'control-btn silence-btn' : 'hidden';
  }


  render() {
    return (
      <div className='display-container'>
        <div className='control-container'>
          <TimeForm hours={this.state.hours} minutes={this.state.minutes} seconds={this.state.seconds} callback={this.formCallback} />
          {/* <button className='control-btn' onClick={() => this.startTimer()}>Start</button> */}
          <button className='control-btn' onClick={() => this.showForm()}>Set Time</button>
          <button className='control-btn' onClick={() => this.resetCountdown()}>Reset</button>
        </div>
        <div className='timer-text'>
        <Countdown date={Date.now() + (1000 * this.convertToSeconds())} autoStart={true} onComplete={this.startBeep} ref={this.clockRef} />
        </div>
        <div>
          <button className={this.silenceButtonClasses()} onClick={this.stopBeep}>Silence!</button>
        </div>
      </div>
    );
  }

}

export default Timer;
